import { ElSelect, ElOption, ElDatePicker, ElCheckbox, ElInput } from "element-plus";
export default {
  name: "AstroForm",
  components: {
    ElSelect,
    ElOption,
    ElDatePicker,
    ElCheckbox,
    ElInput
  },
  props: {
    name: {
      type: String,
      default: ""
    },
    dateType: {
      type: String,
      default: "solar"
    },
    date: {
      type: String,
      default: ""
    },
    timeIndex: {
      type: String,
      default: "0"
    },
    gender: {
      type: String,
      default: "male"
    },
    isLeapMonth: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      timeOptions: [{
        label: "早子时 (00:00-1:00)",
        value: "0"
      }, {
        label: "丑时 (1:00-3:00)",
        value: "1"
      }, {
        label: "寅时 (3:00-5:00)",
        value: "2"
      }, {
        label: "卯时 (5:00-7:00)",
        value: "3"
      }, {
        label: "辰时 (7:00-9:00)",
        value: "4"
      }, {
        label: "巳时 (9:00-11:00)",
        value: "5"
      }, {
        label: "午时 (11:00-13:00)",
        value: "6"
      }, {
        label: "未时 (13:00-15:00)",
        value: "7"
      }, {
        label: "申时 (15:00-17:00)",
        value: "8"
      }, {
        label: "酉时 (17:00-19:00)",
        value: "9"
      }, {
        label: "戌时 (19:00-21:00)",
        value: "10"
      }, {
        label: "亥时 (21:00-23:00)",
        value: "11"
      }, {
        label: "晚子时 (23:00-00:00)",
        value: "12"
      }]
    };
  },
  computed: {
    nameModel: {
      get() {
        return this.name;
      },
      set(value) {
        this.$emit('update:name', value);
      }
    },
    dateTypeModel: {
      get() {
        return this.dateType;
      },
      set(value) {
        this.$emit('update:dateType', value);
      }
    },
    dateModel: {
      get() {
        return this.date;
      },
      set(value) {
        this.$emit('update:date', value);
      }
    },
    timeIndexModel: {
      get() {
        return this.timeIndex;
      },
      set(value) {
        this.$emit('update:timeIndex', value);
      }
    },
    genderModel: {
      get() {
        return this.gender;
      },
      set(value) {
        this.$emit('update:gender', value);
      }
    },
    isLeapMonthModel: {
      get() {
        return this.isLeapMonth;
      },
      set(value) {
        this.$emit('update:isLeapMonth', value);
      }
    }
  },
  methods: {
    handleDateTypeChange() {
      this.dateModel = ""; // 切换日期类型时清空日期
      this.$emit('dateTypeChange');
    },
    handleDateChange() {
      this.isLeapMonthModel = false; // 重置闰月选项
      this.$emit('dateChange');
    },
    // 提供一个重置表单的方法
    resetForm() {
      this.nameModel = "";
      this.dateTypeModel = "solar";
      this.dateModel = "";
      this.timeIndexModel = "0";
      this.genderModel = "male";
      this.isLeapMonthModel = false;
    },
    // 获取表单数据的方法
    getFormData() {
      return {
        name: this.nameModel,
        dateType: this.dateTypeModel,
        date: this.dateModel,
        timeIndex: this.timeIndexModel,
        gender: this.genderModel,
        isLeapMonth: this.isLeapMonthModel
      };
    }
  }
};