import AstroForm from '@/component/aichat/astro-form.vue';
import { ElButton, ElMessage, ElInput } from "element-plus";
import Lunisolar from 'lunisolar';
export default {
  components: {
    AstroForm,
    ElInput
  },
  data() {
    return {
      // 信息
      malename: "",
      maleDateTime: "solar",
      maleDate: "",
      maleTimeIndex: "0",
      maleGender: "male",
      maleIsLeapMonth: false,
      loading: false,
      baziInfo: null,
      showBaziInfo: false,
      baziStr: ''
    };
  },
  methods: {
    // 日期处理方法
    handleMaleDateTypeChange() {
      this.maleDate = ""; // 切换日期类型时清空日期
    },

    handleMaleDateChange() {
      this.maleIsLeapMonth = false; // 重置闰月选项
      this.calculateBazi();
    },
    getCurrentDate() {
      const now = new Date();
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, '0');
      const day = String(now.getDate()).padStart(2, '0');
      this.currentTime = `${year}-${month}-${day}`;
    },
    async generateAstro() {
      if (!this.maleDate) {
        ElMessage.warning('请选择出生日期');
        return;
      }
      this.loading = true;
      try {
        // 信息汇总
        let maleTimeLabel = "";
        switch (this.maleTimeIndex) {
          case "0":
            maleTimeLabel = "早子时 (00:00-1:00)";
            break;
          case "1":
            maleTimeLabel = "丑时 (1:00-3:00)";
            break;
          case "2":
            maleTimeLabel = "寅时 (3:00-5:00)";
            break;
          case "3":
            maleTimeLabel = "卯时 (5:00-7:00)";
            break;
          case "4":
            maleTimeLabel = "辰时 (7:00-9:00)";
            break;
          case "5":
            maleTimeLabel = "巳时 (9:00-11:00)";
            break;
          case "6":
            maleTimeLabel = "午时 (11:00-13:00)";
            break;
          case "7":
            maleTimeLabel = "未时 (13:00-15:00)";
            break;
          case "8":
            maleTimeLabel = "申时 (15:00-17:00)";
            break;
          case "9":
            maleTimeLabel = "酉时 (17:00-19:00)";
            break;
          case "10":
            maleTimeLabel = "戌时 (19:00-21:00)";
            break;
          case "11":
            maleTimeLabel = "亥时 (21:00-23:00)";
            break;
          case "12":
            maleTimeLabel = "晚子时 (23:00-00:00)";
            break;
          default:
            maleTimeLabel = "未知时辰";
            break;
        }
        this.getCurrentDate();
        let prompt = "我需要你对我的八字进行分析，看看我的姻缘状况如何，这是信息：";
        let msg = prompt + "姓名:" + this.malename + ",日期类型:" + (this.maleDateTime === 'solar' ? '阳历' : '农历') + ",生日:" + this.maleDate + ",时辰:" + maleTimeLabel + ",性别:" + (this.malename === 'male' ? '男' : '女');
        if (this.maleDateTime === 'lunar' && this.maleIsLeapMonth) {
          msg += ",闰月:是";
        }
        msg += this.baziStr;
        const encodedDescription = encodeURIComponent(msg);
        try {
          const newWindow = window.open(`/#/chat?user_input=${encodedDescription}`);
          if (!newWindow || newWindow.closed || typeof newWindow.closed === 'undefined') {
            window.location.href = `/#/chat?user_input=${encodedDescription}`;
          }
        } catch (error) {
          window.location.href = `/#/chat?user_input=${encodedDescription}`;
        }
      } catch (error) {
        ElMessage.error('生成姻缘信息时出错');
        console.error(error);
      } finally {
        this.loading = false;
      }
    },
    getBaziStr() {
      this.baziStr = ',八字信息如下：' + this.baziInfo.char8.year.name + ' ' + this.baziInfo.char8.month.name + ' ' + this.baziInfo.char8.day.name + ' ' + this.baziInfo.char8.hour.name;
    },
    // 计算八字信息
    calculateBazi() {
      if (!this.maleDate || !this.maleTimeIndex) {
        this.showBaziInfo = false;
        return;
      }
      try {
        let date;
        let timeStr = "";

        // 验证日期格式
        if (this.maleDateTime === 'solar') {
          if (!/^\d{4}-\d{2}-\d{2}$/.test(this.maleDate)) {
            ElMessage.error('阳历日期格式不正确，请使用YYYY-MM-DD格式');
            return;
          }
        } else {
          if (!/^\d{4}-\d{1,2}-\d{1,2}$/.test(this.maleDate)) {
            ElMessage.error('农历日期格式不正确，请使用YYYY-MM-DD格式');
            return;
          }
        }

        // 根据时辰索引获取对应的小时
        switch (this.maleTimeIndex) {
          case "0":
            timeStr = "00:30";
            break;
          // 早子时 (00:00-1:00)
          case "1":
            timeStr = "02:00";
            break;
          // 丑时 (1:00-3:00)
          case "2":
            timeStr = "04:00:";
            break;
          // 寅时 (3:00-5:00)
          case "3":
            timeStr = "06:00";
            break;
          // 卯时 (5:00-7:00)
          case "4":
            timeStr = "08:00";
            break;
          // 辰时 (7:00-9:00)
          case "5":
            timeStr = "10:00";
            break;
          // 巳时 (9:00-11:00)
          case "6":
            timeStr = "12:00";
            break;
          // 午时 (11:00-13:00)
          case "7":
            timeStr = "14:00";
            break;
          // 未时 (13:00-15:00)
          case "8":
            timeStr = "16:00";
            break;
          // 申时 (15:00-17:00)
          case "9":
            timeStr = "18:00";
            break;
          // 酉时 (17:00-19:00)
          case "10":
            timeStr = "20:00";
            break;
          // 戌时 (19:00-21:00)
          case "11":
            timeStr = "22:00";
            break;
          // 亥时 (21:00-23:00)
          case "12":
            timeStr = "23:30";
            break;
          // 晚子时 (23:00-00:00)
          default:
            timeStr = "12:00:00";
            break;
        }

        // 创建日期对象
        if (this.maleDateTime === 'solar') {
          // 阳历日期直接使用
          try {
            date = new Lunisolar(`${this.maleDate} ${timeStr}`);
            this.baziInfo = date;
            this.getBaziStr();
            console.log(`${this.maleDate} ${timeStr}`);
            if (!date) {
              ElMessage.error('创建日期对象失败，请检查日期是否有效');
              return;
            }
          } catch (error) {
            console.error('创建阳历日期对象失败:', error);
            ElMessage.error('创建阳历日期对象失败，请检查日期是否有效');
            return;
          }
        } else {
          // 农历日期需要特殊处理
          try {
            // 解析农历日期字符串 (格式: YYYY-MM-DD)
            const dateParts = this.maleDate.split('-');
            const year = parseInt(dateParts[0]);
            let month = parseInt(dateParts[1]);
            const day = parseInt(dateParts[2]);

            // 创建农历数据对象
            const lunarData = {
              year: year,
              month: this.maleIsLeapMonth ? month + 100 : month,
              // 如果是闰月，月份值加100
              day: day,
              hour: parseInt(this.maleTimeIndex) // 使用时辰索引
            };

            // 使用fromLunar方法创建Lunisolar实例
            date = Lunisolar.fromLunar(lunarData);
            this.baziInfo = date;
            this.getBaziStr();
            console.log('农历数据:', lunarData);
            if (!date) {
              ElMessage.error('创建农历日期对象失败，请检查日期是否有效');
              return;
            }
          } catch (error) {
            console.error('创建农历日期对象失败:', error);
            ElMessage.error('创建农历日期对象失败，请检查日期是否有效');
            return;
          }
        }

        // 获取八字信息
        this.baziInfo = date;
        if (!this.baziInfo) {
          throw new Error('获取八字信息失败');
        }
        this.showBaziInfo = true;
      } catch (error) {
        console.error('计算八字信息时出错:', error);
        this.showBaziInfo = false;
      }
    }
  }
};